<template>
  <section id="projects" class="section-wrapper">
    <h2 class="section-header">Projects</h2>
    <div class="project">
      <div class="project-overview">
        <h3 class="project-title">Take it, Bake it, Make it</h3>
        <h4 class="project-language">React JS</h4>
        <div class="project-description">
          Take it, Bake it, Make it is a proof of concept project I helped to
          develop during my time in Ahold Delhaize's Propulsion Lab. It gives
          the customer a number of meals to choose from, and the option to have
          them ready to eat straight from the store, ready to put in the oven at
          home, or the ingredients ready to cook themselves. It is comprised of
          a customer facing web app, and a store facing admin app.
        </div>
      </div>
      <b-card no-body>
        <b-tabs card>
          <b-tab title="Customer Side" active>
            <b-container>
              <b-row>
                <b-col lg="8">
                  <h4>Customer Side</h4>
                  <div>
                    The following features were implemented for the customer
                    facing side of this application:
                  </div>
                  <ul>
                    <li>Log in</li>
                    <li>Select a store</li>
                    <li>Select a service (Take, Bake, Make)</li>
                    <li>Browse meals (Can be different based on service selection)</li>
                    <li>
                      View details, including directions for cooking or heating
                      if Bake or Make are selected
                    </li>
                    <li>Add meals to cart</li>
                    <li>Adjust service or quantity from the cart screen</li>
                    <li>Choose a pickup time</li>
                    <li>Receive a confirmation for your order</li>
                  </ul>
                </b-col>
                <b-col lg="4">
                  <CustomerCarousel />
                </b-col>
              </b-row>
            </b-container>
          </b-tab>
          <b-tab title="Admin Side">
            <b-container>
              <b-row>
                <b-col lg="4">
                  <h4>Admin Side</h4>
                  <div>
                    The following features were implemented for the
                    administration facing side of this application:
                  </div>
                  <ul>
                    <li>
                      Active orders: Orders that have been submitted to the
                      store, either pending acceptance or currently being
                      prepared. Can be filtered based on service
                    </li>
                    <li>
                      Ready orders: orders that are completed and waiting to be
                      picked up by the customer.
                    </li>
                    <li>Past orders: Search through previous orders based on date.</li>
                  </ul>
                </b-col>
                <b-col lg="8">
                  <AdminCarousel />
                </b-col>
              </b-row>
            </b-container>
          </b-tab>
        </b-tabs>
      </b-card>
    </div>
    <div class="project">
      <div class="project-overview">
        <h3 class="project-title">Who's Who</h3>
        <h4 class="project-language">React JS</h4>
        <div class="project-description">
          Who's Who was a rapidly developed application for a private
          conference. The goal was to give participants a basic, Facebook or
          Twitter like interface to post their thoughts and opinions regarding
          the conference in real time, and see what other users were saying as
          well. Over the course of two weeks this application was built from the
          ground up, from requirements gathering to design, development, testing
          and deployment. In addition to being one of two front end developers
          on the project, I worked closely with the other parts of the team
          every step of the way.
        </div>
      </div>
      <b-card no-body>
        <b-tabs card>
          <b-tab title="Mobile" active>
            <b-container>
              <b-row>
                <b-col lg="8">
                  <h4>Mobile</h4>
                  <div>
                    This site was designed mobile first. Its focus was on
                    maintaining a simple, self explanatory user experience and
                    clean, clear user interface.
                  </div>
                  <div>The following features were implemented:</div>
                  <ul>
                    <li>Profile with fun facts and relevant information</li>
                    <li>Newsfeed showing posts from everyone at the conference</li>
                    <li>Comments on posts</li>
                    <li>Searching through users</li>
                  </ul>
                </b-col>
                <b-col lg="4">
                  <WWMobileCarousel />
                </b-col>
              </b-row>
            </b-container>
          </b-tab>
          <b-tab title="Desktop">
            <b-container>
              <b-row>
                <b-col lg="4">
                  <h4>Desktop</h4>
                  <div>
                    Making the site desktop friendly had its challenges, such as
                    ensuring support for Internet Explorer and varying screen
                    sizes.
                  </div>
                  <div>The following features were implemented, same as mobile:</div>
                  <ul>
                    <li>Profile with fun facts and relevant information</li>
                    <li>Newsfeed showing posts from everyone at the conference</li>
                    <li>Comments on posts</li>
                    <li>Searching through users</li>
                  </ul>
                </b-col>
                <b-col lg="8">
                  <WWDesktopCarousel />
                </b-col>
              </b-row>
            </b-container>
          </b-tab>
        </b-tabs>
      </b-card>
    </div>
    <div class="project">
      <div class="project-overview">
        <h3 class="project-title">Muse</h3>
        <h4 class="project-language">Swift, Kotlin</h4>
        <div class="project-description">
          Muse is a pet project that started from struggles I have as a
          musician. When writing music, I frequently record short ideas to my
          voice memos to return to later. However, this can quickly become
          cluttered and disorganized, allowing good ideas to fall through the
          cracks. This application hopes to avoid this pitfall by giving the
          user multiple means of organizing their recordings, such as by groups
          or in a calendar view to see their progress later. It is developed for
          both iOS and Android.
        </div>
      </div>
      <b-card no-body>
        <b-tabs card>
          <b-tab title="Android" active>
            <b-container>
              <b-row>
                <b-col lg="8"></b-col>
                <b-col lg="4">
                  <MuseAndroidCarousel />
                </b-col>
              </b-row>
            </b-container>
          </b-tab>
          <b-tab title="iOS">
            <b-container>
              <b-row>
                <b-col lg="8"></b-col>
                <b-col lg="4">
                  <MuseIOSCarousel />
                </b-col>
              </b-row>
            </b-container>
          </b-tab>
        </b-tabs>
      </b-card>
    </div>
  </section>
</template>

